<template>
  <div class="settings">
    <!-- <i
      class="fa-solid fa-angles-left toggle"
      style="color: #ffffff"
      @click="navBar = !navBar"
    ></i> -->
    <img src="@/assets/logo.png" alt="Area19 Logo" />

    <h3>Area 19 - Report Maker</h3>

    <input
      type="file"
      ref="jsonLoader"
      name="jsonLoader"
      style="display: none"
      accept=".a19"
      @change="handleFileUpload"
      aria-labelledby="hiddenjsonLoader"
    />

    <div class="item" id="uploadlogo" @click="openFilePicker">
      <h4>Upload Report File</h4>
      <i class="fa-solid fa-circle-chevron-up" style="color: #ffffff"></i>
    </div>

    <div class="item" id="uploadFile" @click="handleDownload">
      <h4>Download Copy</h4>
      <i class="fa-solid fa-circle-down" style="color: #ffffff"></i>
    </div>

    <hr />

    <div class="item">
      <h4>Help</h4>
      <i class="fa-solid fa-circle-info" style="color: #ffffff"></i>
    </div>
  </div>
</template>

<script>
import { useReportStore } from "/src/store/report.js";

export default {
  data() {
    return {
      navBar: true,
      reportStore: useReportStore(),
    };
  },
  methods: {
    handleDownload() {
      const targetObject = this.reportStore.report;
      const jsonString = JSON.stringify(targetObject);

      // Create a Blob object
      const blob = new Blob([jsonString], { type: "application/json" });

      // Create a URL for the Blob
      const url = URL.createObjectURL(blob);

      // Create an anchor element for downloading
      const a = document.createElement("a");
      a.href = url;
      const currentDate = new Date().toDateString();
      a.download = `${targetObject.patientName}_A19_Report_${currentDate}.a19`; // Set the desired file name

      // Initiate the download
      a.click();

      // Clean up by revoking the URL
      URL.revokeObjectURL(url);
    },

    openFilePicker() {
      const inputElement = this.$refs.jsonLoader;
      inputElement.click();
    },

    async handleFileUpload(event) {
      const file = event.target.files[0];

      if (!file) {
        alert("Please select a JSON file first.");
        return;
      }
      const reader = new FileReader();
      const targetObject = this.reportStore;

      reader.onload = async function (event) {
        try {
          const jsonString = event.target.result;
          const parsedObject = JSON.parse(jsonString);

          // Now you have the parsed object; you can work with it
          // console.log(parsedObject);

          // Use await to ensure updateReport finishes before proceeding
          await targetObject.updateReport(parsedObject);

          // You can call a function here to process the parsed object
          // processParsedObject(parsedObject);
        } catch (error) {
          console.error("Error parsing JSON:", error);
          alert("Error parsing JSON file. Please ensure it is valid JSON.");
        }

        // After updateReport is done, emit the event
        this.$emit("update-data");
      }.bind(this);

      reader.readAsText(file);
    },
  },
};
</script>

<style scoped>
h3 {
  color: white;
}

h4 {
  color: white;
  font-weight: lighter;
}
.settings {
  background-color: #7f5a83;
  background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);

  border-radius: 0 15px 15px 0;
  box-shadow: 0 5px 30px rgba(37, 37, 37, 0.6);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  width: 200px;
  min-width: 220px;
  padding: 10px;
  position: relative;
  text-align: center;
}

.column {
  min-width: 20px;

  background-color: #7f5a83;
  background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);
}

.toggle {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.settings img {
  width: 80%;
}

.item {
  display: flex;
  align-items: center;
  gap: 10px;
  text-align: left;
  height: 30px;
  border-radius: 10px;
  padding: 5px 15px 5px 15px;
  cursor: pointer;
}

.item h4 {
  width: 100%;
}

.item:hover {
  background: #02084149;
  transition: all 0.25s ease;
}

.item img {
  width: 10%;
  align-self: center;
}

.slide-fade-enter-active {
  transition: all 0.3s ease-out;
}

.slide-fade-leave-active {
  transition: all 0.3s ease-out;
}

.slide-fade-enter-from,
.slide-fade-leave-to {
  transform: translateX(-20px);
  opacity: 0;
}
</style>
