.logo-holder {
  display: flex;
  width: 200px;
  height: 70px;
  align-self: center;
  align-items: center;
  justify-content: center;

  cursor: pointer;
  overflow: hidden;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.place-holder {
  display: flex;
  color: #229ed7;
  height: 70%;
  width: 100%;
  align-items: center;
  justify-content: center;
  gap: 5px;

  border-radius: 15px;
  /* From https://css.glass */
  background: rgba(0, 0, 0, 0.06);
  border-radius: 10px;
  transition: all 0.3s ease-out;
}

.place-holder:hover {
  height: 85%;

  color: rgb(35, 156, 212);
  border: 1px solid #229ed7;
}

.hover {
  height: 85%;

  color: rgb(35, 156, 212);
