<template>
  <div style="position: relative">
    <canvas ref="chartCanvas"></canvas>
    <div
      style="
        position: absolute;
        color: rgb(255, 255, 255);
        top: 10px;
        left: 14px;
        width: 38px;
        height: 20px;
        text-align: center;
      "
    >
      <h5>
        {{ LAD }}
      </h5>
    </div>
    <div
      style="
        position: absolute;
        color: rgb(255, 255, 255);
        top: 55px;
        left: 12px;

        width: 38px;
        height: 20px;
        text-align: center;
      "
    >
      <h5>
        {{ RCA }}
      </h5>
    </div>

    <div
      style="
        position: absolute;
        color: rgb(255, 255, 255);
        top: 32px;
        left: 52px;

        width: 35px;
        height: 20px;
        text-align: center;
      "
    >
      <h5>
        {{ LCX }}
      </h5>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: {
      type: String,
      required: false,
    },
    values: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      outerradius: 85 / 2 - 1,
      LAD: null,
      LCX: null,
      RCA: null,
    };
  },
  watch: {
    values() {
      this.setValues();
      this.createChart();
    },
  },
  mounted() {
    this.setValues();

    this.createChart();
  },
  methods: {
    setValues() {
      [this.LAD, this.LCX, this.RCA] = this.values;
    },
    createChart() {
      const canvas = this.$refs.chartCanvas;
      const ctx = canvas.getContext('2d');

      canvas.width = 85;
      canvas.height = 85;

      const centerX = canvas.width / 2;
      const centerY = canvas.height / 2;
      const outerRadius = this.outerradius;
      const innerRadius = outerRadius / 4;

      ctx.clearRect(0, 0, canvas.width, canvas.height);
      // Customize the colors for each section
      //   const sectionColors = ['#a2191a', '#105a93', '#1f7c55'];
      const sectionColors = this.getColors([this.RCA, this.LAD, this.LCX]);
      // Define the data for the chart sections
      const chartData = [1, 1, 1]; // Equal values for each section

      const totalValue = chartData.reduce((total, value) => total + value, 0);

      let startAngle = 0;
      chartData.forEach((value, index) => {
        const sectionAngle = (value / totalValue) * Math.PI * 2;
        const endAngle = startAngle + sectionAngle;

        ctx.beginPath();
        ctx.moveTo(
          centerX + outerRadius * Math.cos(startAngle),
          centerY + outerRadius * Math.sin(startAngle)
        );
        ctx.arc(centerX, centerY, outerRadius, startAngle, endAngle);
        ctx.arc(centerX, centerY, innerRadius, endAngle, startAngle, true);
        ctx.closePath();
        ctx.fillStyle = sectionColors[index];
        ctx.fill();

        // Add stroke between the regions
        ctx.lineWidth = 0.5;
        ctx.strokeStyle = 'black';
        ctx.stroke();

        startAngle = endAngle;
      });

      // Draw white stroke around the outer circle
      ctx.beginPath();
      ctx.arc(centerX, centerY, outerRadius, 0, Math.PI * 2);
      ctx.strokeStyle = '#00000';
      ctx.lineWidth = 0;
      ctx.stroke();

      // Draw white stroke around the inner hole
      ctx.beginPath();
      ctx.arc(centerX, centerY, innerRadius, 0, Math.PI * 2);
      ctx.strokeStyle = '#00000';
      ctx.lineWidth = 0;
      ctx.stroke();
    },
    numberToColorGradient(numberi) {
      let number = numberi;
      // Remove any percentage symbols from the input
      number = number.toString().replace(/%/g, '');

      // Define the color stops and their corresponding percentages
      const colorStops = [
        {
          color: [1, 25, 242],
          minPercentage: 0,
          maxPercentage: 0.23,
        }, // Dark Blue
        {
          color: [0, 227, 252],
          minPercentage: 0.23,
          maxPercentage: 0.45,
        }, // Light Blue
        {
          color: [44, 246, 91],
          minPercentage: 0.45,
          maxPercentage: 0.6,
        }, // Green
        {
          color: [252, 208, 13],
          minPercentage: 0.6,
          maxPercentage: 0.75,
        }, // Yellow
        {
          color: [255, 16, 1],
          minPercentage: 0.75,
          maxPercentage: 1,
        }, // Red
      ];

      // Clamp the input number to the range of -30 to 30
      number = Math.min(Math.max(Number(number), -20), 20);

      // Map the input number to the percentage range of 0 to 1
      const percentage = (number + 20) / 40;

      // Find the corresponding color stops based on the percentage
      let startStop;
      let endStop;
      for (let i = 0; i < colorStops.length; i++) {
        if (
          percentage >= colorStops[i].minPercentage &&
          percentage <= colorStops[i].maxPercentage
        ) {
          startStop = colorStops[i];
          endStop = colorStops[i + 1];
          break;
        }
      }

      // Handle values greater than the last color stop
      if (!endStop) {
        startStop = colorStops[colorStops.length - 2];
        endStop = colorStops[colorStops.length - 1];
      }

      // Perform linear interpolation to calculate the RGB values
      const t =
        (percentage - startStop.minPercentage) /
        (endStop.maxPercentage - startStop.minPercentage);
      const r = Math.round(
        startStop.color[0] + t * (endStop.color[0] - startStop.color[0])
      );
      const g = Math.round(
        startStop.color[1] + t * (endStop.color[1] - startStop.color[1])
      );
      const b = Math.round(
        startStop.color[2] + t * (endStop.color[2] - startStop.color[2])
      );

      return `rgb(${r}, ${g}, ${b})`;
    },
    getColors(arr) {
      return [
        this.numberToColorGradient(arr[0]),
        this.numberToColorGradient(arr[1]),
        this.numberToColorGradient(arr[2]),
      ];
    },
  },
};
</script>

<style scoped>
canvas {
  transform: rotate(60deg);
}

h5 {
  font-weight: 400;
  font-size: 12px;
}
</style>
