<template>
  <Transition>
    <div v-if="segment">
      <p>{{ segment.replace("AHA", "Segment ") }}</p>

      <p>Value:</p>
      <input
        type="text"
        class="input"
        ref="segmentValue"
        :value="reportStore.report[segment]"
        maxlength="5"
        @input="updateValue"
        @blur="stopEditing"
      />
    </div>
  </Transition>
</template>

<script>
import { useReportStore } from "/src/store/report.js";

export default {
  props: {
    segment: { type: String },
  },

  data() {
    return {
      reportStore: useReportStore(),
    };
  },

  mounted() {
    if (this.segment) {
      this.$refs.segmentValue.focus();
    }
  },
  methods: {
    updateValue() {
      this.reportStore.report[this.segment] = this.$refs.segmentValue.value;
      this.$emit("update", this.segment);
    },

    stopEditing() {
      this.$emit("off", this.segment);
    },
  },
  updated() {
    if (this.$refs.segmentValue) {
      this.$refs.segmentValue.focus();
    }
  },

  //   watch: {
  //     segment() {
  //       this.$refs.segmentValue.focus();
  //     },
  //   },
};
</script>

<style scoped>
p {
  line-height: 5px;
}
.input {
  width: 50px;
  background: none;
  border: 1px dashed #229ed7;
  color: rgb(0, 0, 0);
  outline: none;
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.3s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}
</style>
