.polarMap-container {
  position: relative;
  display: flex;
  gap: 5px;
  align-items: center;
  justify-content: center;
  background: #f8f8f8;
  max-height: 130px;
  /* overflow: hidden; */
  /* border: 1px solid black; */
}

.tag {
  position: absolute;
  color: rgb(0, 0, 0);
  text-align: center;
  background: #ffffff00;

  display: flex;
  flex-direction: column;
  padding: 2px;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  left: -75px;
}

.hidden {
  opacity: 0;
}

.LCX {
  left: 75px;
  top: 50px;
  width: 45px;
}

.RCA {
  left: 15px;
  top: 85px;
  width: 65px;
}
.bar-container {
  display: flex;
}
.values {
  position: relative;
  width: 15px;
  font-size: 12px;
}

.end {
  position: absolute;
  top: -15px;
  right: 2px;
}

.start {
  position: absolute;
  bottom: -15px;
  right: 2px;
}

.color-bar {
  width: 7px;
  border-radius: 2px;
  height: 120px;
  border: 1px solid black;
}

