<template>
  <input
    @input="updateProp"
    @blur="stopEditing"
    :value="this.reportStore.report[this.id]"
    :maxlength="maxLength"
    ref="inputField"
    class="input-field"
    :class="{ edit: editable }"
    :style="{ paddingLeft: editable ? '5px' : '0px' }"
  />
</template>

<script>
import { useReportStore } from "/src/store/report.js";

export default {
  props: {
    id: { type: String, required: true },
    editable: { type: Boolean, default: true },
    maxLength: { type: Number, default: 5 },
  },
  data() {
    return {
      editing: 0,
      reportStore: useReportStore(),
    };
  },
  methods: {
    stopEditing() {
      // this.$emit("updateValue", this.id, this.editedValue);
      this.reportStore.report[this.id] = this.$refs.inputField.value;
    },
    updateProp(event) {
      this.reportStore.report[this.id] = this.$refs.inputField.value;
    },
  },
  watch: {
    editable() {
      this.$refs.inputField.readOnly = !this.editable;
      this.reportStore.report[this.id] = this.$refs.inputField.value;
    },
  },
  mounted() {
    // this.$refs.inputField.value = this.reportStore.report[this.id];

    if (!this.editable) {
      this.$refs.inputField.readOnly = true;
    }
  },
};
</script>

<style scoped>
h5 {
  color: #000917;
  font-weight: bold;
  font-size: 13px;
  height: 17px;
  width: 50px;
}

.input-field {
  color: #000917;
  font-weight: bold;
  font-size: 13px;
  outline: none;
  width: 50px;
  height: 17px;
  overflow: hidden;
  border: 1px dashed #ffffff00;
  transition: all 0.3s ease;
  background: none;
}

.input-field.edit {
  border: 1px dashed #229ed7;
}
</style>
