<template>
  <div
    class="reportcontainermain"
    :style="{
      width: width + 'px',
      height: height + 'px',
    }"
  >
    <div class="toolbar-report-container">
      <div class="reporthere" :style="{ height: height - 15 + 'px' }">
        <ReportHtml ref="editablereport" :editable="edit" :key="key" />
      </div>

      <div class="toolbar">
        <button class="tool" @click="handleEdit" :class="{ clicked: edit }">
          <h1>Edit</h1>
        </button>

        <div style="position: relative">
          <button class="tool" @click="handleSave">
            <h1>Save</h1>
          </button>
          <div class="popup" :class="{ show: showPopup }">
            <h5>Please stop editing first.</h5>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ReportHtml from "@/components/ReportHtml.vue";

export default {
  components: {
    ReportHtml,
  },

  props: {
    width: { type: Number },
    height: { type: Number },
  },
  methods: {
    printReport() {
      this.$refs.editablereport.buildPDF();
    },
    handleEdit() {
      this.edit = !this.edit;
    },
    handleSave() {
      if (this.edit) {
        // Show the popup for 3 seconds
        this.showPopup = true;
        setTimeout(() => {
          this.showPopup = false;
        }, 3000); // 3000 milliseconds = 3 seconds
      } else {
        this.$refs.editablereport.downloadReport();
      }
    },
  },
  data() {
    return {
      edit: true,
      showPopup: false,
      key: null,
    };
  },
};
</script>

