<template>
  <div v-if="editable" class="options-container" ref="optionsContainer">
    <Transition name="bounce">
      <div v-if="selectContainer" class="container-list">
        <div class="coronary-choices">
          <h4>Select Impacted Coronary Region(s):</h4>
          <div class="choices-container">
            <div
              v-for="(value, choice) in choices"
              :key="choice"
              class="choice"
              @click="handleChoice(choice)"
            >
              <!-- <input type="checkbox" id="vehicle1" name="vehicle1" value="Bike" /> -->
              <i
                v-if="value"
                class="fa-solid fa-circle-check fa-sm"
                style="color: #ffffff"
              ></i>

              <i
                v-else
                class="fa-regular fa-circle fa-sm"
                style="color: #ffffff"
              ></i>
              <label for="vehicle1"> {{ choice }}</label>
            </div>
          </div>
          <div class="choice" @click="handleOptionSelect">
            <label>Add Finding</label>
          </div>
        </div>
      </div>
    </Transition>

    <div
      class="select-options-container"
      @click="toggleContainer"
      @mouseenter="startHoverTimer"
      @mouseleave="clearHoverTimer"
    >
      <i
        class="fa-regular fa-rectangle-list"
        :style="{ color: selectContainer ? 'white' : null }"
      ></i>
    </div>
  </div>
</template>

<script>
import { useReportStore } from "/src/store/report.js";

export default {
  props: {
    editable: { type: Boolean },
  },

  data() {
    return {
      selectContainer: false,

      reportStore: useReportStore(),
      patientConditions: {},
      choices: {
        Normal: false,
        Global: false,
        LAD: false,
        LCX: false,
        RCA: false,
      },

      cannedText: {
        Normal:
          "There is a normal response of the myocardial oxygenation to a breathing challenge, reflecting normal coronary vascular dysfunction.",
        Global:
          "There is a blunted response of the myocardial oxygenation to a breathing challenge, reflecting global coronary vascular dysfunction on a microvascular level.",
        LAD: "There is a blunted response of the myocardial oxygenation to a breathing challenge, reflecting coronary vascular dysfunction, predominantly in the anterior, anteroseptal, and anterolateral myocardium. This finding is consistent with significant stenosis in the proximal LAD.",
        LCX: "There is coronary vascular dysfunction, predominantly in the inferolateral myocardium. This finding is consistent with significant stenosis in the proximal LCX.",
        RCA: "There is coronary vascular dysfunction, predominantly in the inferior and inferoseptal myocardium. This finding is consistent with significant stenosis in the proximal RCA.",
        LADLCX:
          "There is coronary vascular dysfunction in the anterior, anteroseptal, anterolateral, and inferolateral myocardium. This finding is consistent with significant stenosis in the proximal LAD and LCX.",
        LADRCA:
          "There is coronary vascular dysfunction in the anterior, anteroseptal, and anterolateral, as well as inferoseptal and inferior myocardium. This finding is consistent with significant stenosis in the proximal LAD and RCA.",
        LCXRCA:
          "There is coronary vascular dysfunction in the inferolateral, inferoseptal and inferior myocardium. This finding is consistent with significant stenosis in the proximal LCX and RCA.",
      },
    };
  },

  methods: {
    handleOptionSelect() {
      if (this.choices.Global) {
        this.reportStore.report["summary"] = this.cannedText.Global;
      } else {
        let selected = Object.keys(this.choices)
          .map((key) => (this.choices[key] ? key : ""))
          .join("");
        this.reportStore.report["summary"] = this.cannedText[selected];
      }
      this.toggleContainer();
    },

    startHoverTimer() {
      this.hoverTimer = setTimeout(() => {
        // The mouse has hovered for at least half a second
        if (!this.selectContainer) {
          this.toggleContainer();
        }
      }, 500); // 500 milliseconds = half a second
    },
    clearHoverTimer() {
      if (this.hoverTimer) {
        clearTimeout(this.hoverTimer);
        this.hoverTimer = null;
      }
    },

    handleChoice(choice) {
      this.choices[choice] = !this.choices[choice];

      if (choice === "Global") {
        for (const key in this.choices) {
          if (key !== "Normal") {
            this.choices[key] = this.choices[choice];
          } else {
            this.choices[key] = false;
          }
        }
      } else if (choice === "Normal") {
        for (const key in this.choices) {
          if (key !== "Normal") {
            this.choices[key] = false;
          }
        }
      } else if (this.choices.Global || this.choices.Normal) {
        this.choices.Global = false;
        this.choices.Normal = false;
      } else if (this.choices.LAD && this.choices.LCX && this.choices.RCA) {
        this.choices.Global = true;
      }
    },

    toggleContainer() {
      this.clearHoverTimer();

      this.selectContainer = !this.selectContainer;

      if (this.selectContainer) {
        this.$refs.optionsContainer.style.pointerEvents = "all";
      } else {
        this.$refs.optionsContainer.style.pointerEvents = "none";
      }
    },
  },
};
</script>

<style scoped>
.options-container {
  pointer-events: none;
  position: absolute;
  width: 100%;
  max-height: 100%;
  height: 100%;
  border-radius: 5px;
}
.container-list {
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(
      to right,
      #182d36,
      #142429,
      #142429e1,
      #0f2027b7
    ),
    url(https://grainy-gradients.vercel.app/noise.svg); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #182d36, #142429, #142429e1, #0f2027b7),
    url(https://grainy-gradients.vercel.app/noise.svg); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  border-radius: 5px;
  width: 100%;
  height: 100%;
  max-height: 100%;
  overflow: scroll;
  overflow-x: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 10px;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.container-list::-webkit-scrollbar {
  display: none;
  width: 0 !important;
}

.container-list h4 {
  color: white;
  line-height: 5px;
}

.coronary-choices {
  padding-left: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.choices-container {
  display: flex;
  gap: 15px;
}

.coronary-choices label {
  color: white;
  font-size: 13px;
  pointer-events: none;
  font-weight: bold;
}

.choice {
  border: 1px solid rgb(156, 155, 155);

  width: fit-content;
  padding: 5px 8px 5px 8px;
  display: flex;
  align-items: center;
  gap: 5px;
  border-radius: 5px;
  cursor: pointer;
}

.choice:hover {
  background: rgba(156, 155, 155, 0.356);
  transition: all 0.3s ease;
}

.coronary-choices input {
  font-size: 13px;
  cursor: pointer;
}

.option {
  color: white;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
  cursor: pointer;
  background: #2020209f;
}

.option:hover {
  background: #229ed78f;
}

.select-options-container {
  position: absolute;
  right: 5px;
  top: 5px;
  pointer-events: all;
  cursor: pointer;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
}

.select-options-container:hover {
  background: #0f2027;
  color: white;
  transition: all 0.3s ease;
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}

.bounce-enter-active {
  animation: bounce-in 0.3s;
}
.bounce-leave-active {
  opacity: 0;
  transition: all 0.3s ease;
}
@keyframes bounce-in {
  0% {
    opacity: 0.5;
    transform: translateX(200px);
  }

  100% {
    opacity: 1;
  }
}
</style>
