<script>
import { useReportStore } from "/src/store/report.js";
import html2canvas from "html2canvas";

export default {
  props: {
    visible: { type: Array },
    editable: { type: Boolean },
    id: { type: String },
  },
  data() {
    return {
      radius: 60,
      reportStore: useReportStore(),
      colorStops: [
        {
          color: [0, 0, 0],
          minPercentage: 0,
          maxPercentage: 0,
        }, // Black
        {
          color: [30, 30, 232],
          minPercentage: 0.25,
          maxPercentage: 0.5,
        }, // Blue
        {
          color: [60, 224, 59],
          minPercentage: 0.5,
          maxPercentage: 0.75,
        }, // Green
        {
          color: [251, 0, 0],
          minPercentage: 0.75,
          maxPercentage: 0.75,
        }, // Red
        {
          color: [255, 255, 255],
          minPercentage: 1,
          maxPercentage: 1,
        }, // White
      ],

      hideLAD: true,
      hideLCX: true,
      hideRCA: true,
    };
  },

  mounted() {
    this.setColor("LAD");
    this.setColor("RCA");
    this.setColor("LCX");
  },
  methods: {
    handleClickGlobal(e) {
      const center = this.checkCenter(e.offsetX, e.offsetY);
      if (center) {
        this.$emit("segment-updated", -1);
      }
    },
    handleClickRegion(target) {
      this.reportStore.report.coronaryTerritoryLAD = "37.1%";
    },
    checkCenter(x1, y1) {
      const distance = this.distance(x1, y1, this.radius, this.radius);

      return distance < this.radius * 0.25;
    },
    distance(x1, y1, x2, y2) {
      return Math.sqrt((x2 - x1) * (x2 - x1) + (y2 - y1) * (y2 - y1));
    },

    setColor(target) {
      if (this.reportStore.report[`coronaryTerritory${target}`] > 0) {
        this.$refs[target].style.fill = this.getColorAtPercentage(
          this.reportStore.report[`coronaryTerritory${target}`]
        );
      } else {
        this.$refs[target].style.fill = "#869FD5";
      }
    },
    handleHide() {
      this.hideLAD = !this.reportStore.report.coronaryTerritoryLAD > 0;
      this.hideLAD = !this.reportStore.report.coronaryTerritoryLAD > 0;
    },
    getColorAtPercentage(percentage) {
      if (!parseFloat(percentage)) {
        return "rgb(0,0,0)";
      }
      const gradient =
        "linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(30,30,232,1) 25%, rgba(60,224,59,1) 50%, rgba(251,0,0,1) 75%, rgba(255,255,255,1) 100%)";

      const stops = gradient.match(
        /rgba?\([\d,]+\s?,\s?[\d,]+\s?,\s?[\d,]+\s?,\s?[\d.]+\)/g
      );
      if (!stops || stops.length < 2) {
        throw new Error("Invalid gradient format");
      }

      const clampedPercentage = Math.min(
        Math.max(parseFloat(percentage), 0),
        100
      );
      const index = (clampedPercentage / 100) * (stops.length - 1);
      const lowerIndex = Math.floor(index);
      const upperIndex = Math.ceil(index);
      const factor = index - lowerIndex;

      const lowerColor = stops[lowerIndex].match(/[\d.]+/g).map(Number);
      const upperColor = stops[upperIndex].match(/[\d.]+/g).map(Number);

      const interpolatedColor = lowerColor.map((value, i) =>
        Math.round(value + factor * (upperColor[i] - value))
      );

      return `rgb(${interpolatedColor.join(", ")})`;
    },

    async saveImg() {
      const divElement = this.$refs["map-container"];

      const l = divElement.getBoundingClientRect();

      console.log(l.width, l.height);
      // Use html2canvas to capture a screenshot of the div
      html2canvas(divElement, { logging: false, scale: 3 })
        .then((canvas) => {
          // Convert the canvas to an image data URL
          const imageDataURL = canvas.toDataURL("image/png");
          this.reportStore.report[this.id] = imageDataURL;

          // Emit the image data URL to the parent component
          return imageDataURL;
        })
        .catch((error) => {
          console.error("Error capturing screenshot:", error);
        });
    },
  },
  computed: {
    colorBarStyle() {
      return {
        background: `linear-gradient(to top, ${this.colorStops
          .map(
            (stop) =>
              `rgb(${stop.color.join(",")}) ${stop.minPercentage * 100}%`
          )
          .join(", ")})`,
      };
    },
    hasValues() {
      // Double the input string
      const store = this.reportStore.report;
      return [
        parseFloat(store.coronaryTerritoryLAD) > 0,
        parseFloat(store.coronaryTerritoryLCX) > 0,
        parseFloat(store.coronaryTerritoryRCA) > 0,
      ];
    },
  },

  watch: {
    "reportStore.report.coronaryTerritoryLAD": function () {
      this.setColor("LAD");
    },
    "reportStore.report.coronaryTerritoryLCX": function () {
      this.setColor("LCX");
    },
    "reportStore.report.coronaryTerritoryRCA": function () {
      this.setColor("RCA");
    },
    async editable() {
      if (!this.editable) {
        this.saveImg();
      }
    },
  },
};
</script>

