  width: 45px;
}

.RCA {
  left: 15px;
  top: 90px;
  width: 65px;
}
.bar-container {
  display: flex;
}
.values {
  position: relative;
  width: 15px;
  font-size: 12px;
}

.end {
  position: absolute;
  top: -15px;
  right: 2px;
}

.start {
  position: absolute;
  bottom: -15px;
  right: 2px;
}

.color-bar {
  width: 7px;
  border-radius: 2px;
  height: 120px;
  border: 1px solid black;
}

#Path_120 {
  filter: drop-shadow(10px 10px 10px rgba(0, 0, 0, 1) inset);
}

