<template>
  <div class="textarea-container">
    <SelectOptions :editable="editable" />
    <textarea
      :value="reportStore.report[id]"
      @input="updateProp"
      @blur="stopEditing"
      ref="inputField"
      rows="7"
      maxlength="500"
      :class="{ edit: editable, noedit: !editable }"
      :placeholder="placeholder"
      aria-labelledby="textAreaInReportEditable"
    ></textarea>
  </div>
</template>

<script>
import { useReportStore } from "/src/store/report.js";

import SelectOptions from "./SelectOptions.vue";

export default {
  components: {
    SelectOptions,
  },
  props: {
    id: { type: String, required: true },
    placeholder: { type: String, default: "" },
    editable: { type: Boolean, default: true },
  },
  data() {
    return {
      editing: 0,
      editedValue: null,
      selectContainer: true,
      reportStore: useReportStore(),
    };
  },
  methods: {
    stopEditing() {
      // this.$emit("updateValue", this.id, this.editedValue);
      this.reportStore.report[this.id] = this.$refs.inputField.value;
    },
    updateProp(event) {
      this.reportStore.report[this.id] = event.target.value;
    },
    splitStringByPixelWidth(inputString, maxWidthInPixels, charWidthInPixels) {
      if (inputString === "") {
        return [""];
      }
      if (maxWidthInPixels <= 0 || charWidthInPixels <= 0) {
        throw new Error(
          "maxWidthInPixels and charWidthInPixels should be greater than 0"
        );
      }

      const words = inputString.split(" ");
      const lines = [];
      let currentLine = "";
      let currentWidth = 0;

      for (let i = 0; i < words.length; i++) {
        const word = words[i];
        const wordWidth = word.length * charWidthInPixels;

        if (currentWidth + wordWidth <= maxWidthInPixels) {
          if (currentLine.length > 0) {
            currentLine += " ";
            currentWidth += charWidthInPixels;
          }
          currentLine += word;
          currentWidth += wordWidth;
        } else {
          lines.push(currentLine);
          currentLine = word;
          currentWidth = wordWidth;
        }
      }

      if (currentLine) {
        lines.push(currentLine);
      }

      return lines;
    },
    processSentences(inputString) {
      // console.log(inputString);
      const linesFromUserInput = inputString.split("\n");

      const linesToAdd = [];

      linesFromUserInput.forEach((line) => {
        const splitByWidth = this.splitStringByPixelWidth(line, 1090, 12);
        linesToAdd.push(...splitByWidth);
      });

      return linesToAdd;
    },

    handleInput() {
      const textarea = this.$refs.inputField;
      // console.log("input");

      const numLines = this.processSentences(textarea.value);

      if (numLines.length > 7) {
        textarea.value = numLines.slice(0, 7).join("\n");
      }
    },
  },
  mounted() {
    if (!this.editable) {
      this.$refs.inputField.readOnly = true;
    }
    const textarea = this.$refs.inputField;
    const maxLines = 7;
    textarea.addEventListener("input", this.handleInput);
  },
  watch: {
    editable() {
      this.$refs.inputField.readOnly = !this.editable;
    },
  },
};
</script>

<style scoped>
.textarea-container {
  margin-top: 7px;
  margin-bottom: 7px;
  display: flex;
  width: 100%;
  position: relative;
}

h5 {
