import { defineStore } from "pinia";

export const useReportStore = defineStore({
  id: "report-store",
  state: () => ({
    report: {
      reportTitle: "BE-OS CMR Report",
      reportLogo: null,

      patientName: "PATIENT NAME",
      patientGender: "",
      patientAge: "00",
      patientDOB: "",
      patientID: "123 456 789",

      rbhNumber: "000 000 000",
      protocol: "OS-CMR",
      scanDate: "",
      scanTime: "00:00",

      completeData: "",
      imageQuality: "",

      reader: "Reader Name, MD",
      verifier: "",

      scannerSystem: "Philips Ingenia",
      fieldStrength: "1.5T",
      softwareVersion: "5.7.1.3",
      starClaimer:
        "*Normal values are continuously being updated, therefore, may be subject to changes",

      summary: "",

      heartRateBaseline: "00",
      // heartRateBaselineNormalRange: "55-85",
      heartRateHV: "00",
      heartRateBH: "00",
      // heartRateBHNormalRange: "55-85",
      dtheartrate: "00",
      // dtheartrateNormalRange: ">15",

      vascularGlobal: "00",
      vascularLAD: "00",
      vascularLCX: "00",
      vascularRCA: "00",
      normalRangeVascular: ">9.1",

      strainLADBaseline: "00",
      strainLADHV: "00",
      strainLADBH: "00",

      strainLCXBaseline: "00",
      strainLCXHV: "00",
      strainLCXBH: "00",

      strainRCABaseline: "00",
      strainRCAHV: "00",
      strainRCABH: "00",

      strainGLOBALBaseline: "00",
      strainGLOBALHV: "00",
      strainGLOBALBH: "00",

      vascularImage: { img: null, x: 0, y: 0, zoom: 1, final: null },
      strainImage: { img: null, x: 0, y: 0, zoom: 1, final: null },

      oxygenSensitiveMap: "",
      AHA1: "",
      AHA2: "",
      AHA3: "",
      AHA4: "",
      AHA5: "",
      AHA6: "",
      AHA7: "",
      AHA8: "",
      AHA9: "",
      AHA10: "",
      AHA11: "",
      AHA12: "",

      strainMapImage: "",

      coronaryTerritoryLAD: "00",
      coronaryTerritoryLCX: "00",
      coronaryTerritoryRCA: "00",

      strainMapBaseline: "",
      strainMapHV: "",
      strainMapBH: "",
      normalRange: "<-20%",
    },
  }),
  getters: {
    reportData() {
      return this.report;
    },
    //
  },
  actions: {
    updateReport(newReport) {
      const oldReport = this.report;
      // Iterate through each key in the newReport object
      for (let key in newReport) {
        if (typeof newReport[key] === "object") {
          if (!oldReport[key]) {
            oldReport[key] = {};
          }
          for (let subKey in newReport[key]) {
            oldReport[key][subKey] = newReport[key][subKey];
          }
        } else {
          oldReport[key] = newReport[key];
        }
      }
      // console.log("done");
    },
    //
  },
});
