<template>
  <div
    id="image-container"
    ref="imageContainer"
    class="image-container"
    :class="{ moving: moveMode, hover: hovering }"
    :style="{ height: height + 'pt', width: '98%' }"
    @dragenter="handleDragOver"
    @dragover="handleDragOver"
    @drop="handleImageDrop"
    @dragleave="handleDragLeave"
  >
    <Transition>
      <div v-if="editable && !moveMode" class="buttons">
        <div
          class="upload-button"
          title="Upload File"
          alt="Upload File"
          style="position: relative"
        >
          <button class="upload-buttons upload-image" @click="openFilePicker">
            <i class="fas fa-upload fa-lg" style="color: #ffffff"></i>
          </button>
          <input
            type="file"
            ref="imageLoaderInput"
            name="imageLoader"
            style="display: none"
            accept="image/jpeg, image/png, image/svg"
            @change="uploadImage"
            aria-labelledby="hiddenImageInputinReport"
            max="4MB"
          />
        </div>

        <div
          v-if="originalImage"
          class="move-button"
          title="Move"
          alt="Move"
          style="position: relative"
        >
          <button
            class="upload-buttons"
            :class="{ moving: enableMoving }"
            style="left: 50px"
            @click="enableMoving = !enableMoving"
          >
            <i class="fas fa-arrows fa-lg" style="color: #ffffff"></i>
          </button>
        </div>

        <div
          v-if="originalImage"
          class="zoom-in-button"
          title="Zoom In"
          alt="Zoom In"
          style="position: relative"
        >
          <button class="upload-buttons" style="left: 90px" @click="zoomIn">
            <i class="fas fa-search-plus fa-lg" style="color: #ffffff"></i>
          </button>
        </div>

        <div
          v-if="originalImage"
          class="zoom-out-button"
          title="Zoom Out"
          alt="Zoom Out"
          style="position: relative"
        >
          <button class="upload-buttons" style="left: 130px" @click="zoomOut">
            <i class="fas fa-search-minus fa-lg" style="color: #ffffff"></i>
          </button>
        </div>

        <div
          v-if="originalImage"
          class="zoom-out-button"
          title="Center Image"
          alt="Center Image"
          style="position: relative"
        >
          <button
            class="upload-buttons"
            style="
              left: 170px;
              display: flex;
              align-items: center;
              justify-content: center;
            "
            @click="resetImage"
          >
            <i
              class="fa-solid fa-arrows-to-circle fa-lg"
              style="color: #ffffff"
            ></i>
          </button>
        </div>
      </div>
    </Transition>

    <div ref="vascular-image-div">
      <div style="position: relative" v-if="addBar">
        <img
          src="@/assets/images/bar.png"
          class="bar-graph-legend-os"
          alt="Image Not Loaded"
        />
      </div>

      <div
        class="target-image-container"
        ref="zoomControl"
        @mousedown="handleMouseDown"
        @mousemove="handleMouseMove"
        @mouseup="handleMouseUp"
        @mouseleave="handleMouseUp"
      >
        <img class="target-image" alt="" ref="targetImage" />
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from "html2canvas";
import { useReportStore } from "/src/store/report.js";

export default {
  props: {
    id: { type: String },
    width: { type: Number, default: 200 },
    height: { type: Number, default: 200 },
    canvasText: { type: String, default: "Select or Drop Image" },
    addBar: { type: Boolean, default: false },
    editable: { type: Boolean, default: true },
    imgSource: { type: String, default: "left" },
  },
  data() {
    return {
      isMoving: false,
      canvasX: 0,
      canvasY: 0,
      zoomValue: 1,
      originalImage: false,

      viewSample: false,

      hovering: false,

      moveMode: false,
      enableMoving: false,
      cStartX: null,
      cStartY: null,

      currentZoom: 1,
      reportStore: useReportStore(),
    };
  },
  methods: {
    // Image Edit Handeling:
    zoomIn() {
      const target = this.$refs.targetImage;
      const scale = parseFloat(target.style.transform.match(/-?\d+\.\d+/));
      if (scale) {
        this.currentZoom = scale;
      }
      this.currentZoom *= 1.05;
      target.style.transform = `scale(${this.currentZoom})`;
      const targetStore = this.reportStore.report;
      targetStore[this.id].zoom = this.currentZoom;
      this.centerImage();
    },

    zoomOut() {
      const target = this.$refs.targetImage;
      const scale = parseFloat(target.style.transform.match(/-?\d+\.\d+/));
      if (scale) {
        this.currentZoom = scale;
      }
      this.currentZoom *= 0.95;

      target.style.transform = `scale(${this.currentZoom})`;
      const targetStore = this.reportStore.report;
      targetStore[this.id].zoom = this.currentZoom;

      const currentLeft = parseFloat(target.style.left.replace("px", "")) || 0;
      const currentTop = parseFloat(target.style.top.replace("px", "")) || 0;
      this.centerImage();
    },
    handleMouseDown(e) {
      if (this.editable && this.enableMoving) {
        this.cStartX = e.offsetX;
        this.cStartY = e.offsetY;
        this.moveMode = true;
      }
    },
    handleMouseUp() {
      this.moveMode = false;
    },
    handleMouseMove(e) {
      if (this.moveMode) {
        const dx = e.offsetX - this.cStartX;
        const dy = e.offsetY - this.cStartY;
        this.cStartX = e.offsetX;
        this.cStartY = e.offsetY;
        const target = this.$refs.targetImage;
        const currentLeft =
          parseFloat(target.style.left.replace("px", "")) || 0;
        const currentTop = parseFloat(target.style.top.replace("px", "")) || 0;

        const targetStore = this.reportStore.report;

        targetStore[this.id].x = currentLeft + dx;
        targetStore[this.id].y = currentTop + dy;

        target.style.left = `${currentLeft + dx}px`;
        target.style.top = `${currentTop + dy}px`;
      }
    },

    resetImage() {
      this.centerImage();
      this.scaleToFit();
    },

    resetProperties() {
      const targetImage = this.$refs.targetImage;
      targetImage.style.transform = `scale(1)`;
      targetImage.style.left = `0px`;
      targetImage.style.top = `0px`;
    },

    centerImage() {
      const targetImage = this.$refs.targetImage;
      const imageContainer = this.$refs.imageContainer.getBoundingClientRect();
      const x = (imageContainer.width - targetImage.width) / 2;
      const y = (imageContainer.height - targetImage.height) / 2;
      targetImage.style.left = `${x}px`;
      targetImage.style.top = `${y}px`;
      const targetStore = this.reportStore.report;
      targetStore[this.id].x = x;
      targetStore[this.id].y = y;
    },

    scaleToFit() {
      const targetImage = this.$refs.targetImage;
      const imageContainer = this.$refs.imageContainer.getBoundingClientRect();
      const scaleFactor = imageContainer.width / targetImage.width;
      this.currentZoom = 1 * scaleFactor;
      const targetStore = this.reportStore.report;
      targetStore[this.id].zoom = this.currentZoom;
      targetImage.style.transform = `scale(${this.currentZoom})`;
    },

    // Image Upload Handeling:
    openFilePicker() {
      this.$refs.imageLoaderInput.click();
    },

    uploadImage(event) {
      // event.preventDefault();
      const selectedFile = event.target.files[0];
      this.handleFileUpload(selectedFile);
    },

    handleFileUpload(selectedFile) {
      this.resetProperties();
      if (selectedFile) {
        const targetImage = this.$refs.targetImage;
        const targetStore = this.reportStore.report;
        const id = this.id;
        this.currentZoom = 1;
        const currentZoom = this.currentZoom;
        const currentLeft =
          parseFloat(targetImage.style.left.replace("px", "")) || 0;
        const currentTop =
          parseFloat(targetImage.style.top.replace("px", "")) || 0;
        this.imageToBase64(selectedFile, function (base64String) {
          targetImage.src = base64String;
          targetStore[id].img = base64String;
          targetStore[id].x = currentLeft;
          targetStore[id].y = currentTop;
          targetStore[id].zoom = currentZoom;
        });
      }
      this.originalImage = true;
    },

    handleDragOver(event) {
      event.preventDefault();

      this.hovering = true;
    },

    handleDragLeave(event) {
      event.preventDefault();

      this.hovering = false;
    },

    handleImageDrop(event) {
      if (!this.editable) {
        event.preventDefault();
        this.hovering = false;
        return;
      }
      this.hovering = false;
      event.preventDefault();
      const selectedFile = event.dataTransfer.files[0];

      // Check if the file size exceeds 4MB (4 * 1024 * 1024 bytes).
      if (selectedFile.size > 4 * 1024 * 1024) {
        // File size exceeds 4MB, handle the error.
        console.log("File size exceeds 4MB. Please choose a smaller file.");
        return;
      }

      // Check if the file type is one of the accepted image formats (e.g., JPEG, PNG, GIF).
      const acceptedFormats = ["image/jpeg", "image/png", "image/svg"];
      if (!acceptedFormats.includes(selectedFile.type)) {
        // File format is not accepted, handle the error.
        console.log(
          "Invalid file format. Please choose a JPEG, PNG, or GIF image."
        );
        return;
      }

      // If the file size and format are both acceptable, proceed with handling the file.

      this.handleFileUpload(selectedFile);
    },

    imageToBase64(file, callback) {
      const reader = new FileReader();

      reader.onload = function (e) {
        if (typeof callback === "function") {
          callback(e.target.result);
        }
      };

      reader.readAsDataURL(file);
    },

    loadImgFromStore(imgData) {
      this.$refs.targetImage.src = imgData.img;

      const target = this.$refs.targetImage;
      target.style.transform = `scale(${imgData.zoom})`;
      this.currentZoom = imgData.zoom;
      target.style.left = `${imgData.x}px`;
      target.style.top = `${imgData.y}px`;
      this.originalImage = true;
    },

    async saveImg() {
      const divElement = this.$refs["vascular-image-div"];

      // Use html2canvas to capture a screenshot of the div
      html2canvas(divElement, { logging: false, scale: 3 })
        .then((canvas) => {
          // Convert the canvas to an image data URL
          const imageDataURL = canvas.toDataURL("image/png");
          this.reportStore.report[this.id].final = imageDataURL;

          // Emit the image data URL to the parent component
          return imageDataURL;
        })
        .catch((error) => {
          console.error("Error capturing screenshot:", error);
        });
    },
  },
  mounted() {
    const targetContainer = this.$refs.imageContainer.getBoundingClientRect();
    this.$refs.zoomControl.style.width = `${targetContainer.width}px`;
    this.$refs.zoomControl.style.height = `${targetContainer.height - 2}px`;

    if (this.reportStore.report[this.id].img) {
      this.loadImgFromStore(this.reportStore.report[this.id]);
    }
  },
  watch: {
    async editable() {
      if (this.moveMode) {
        this.moveMode = !this.moveMode;
        this.handleMouseUp();
      }

      if (!this.editable) {
        this.saveImg();
      }
    },
  },
};
</script>

<style scoped>
.target-image {
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  left: 0px;
  top: 0px;
}
.target-image-container {
  position: relative;
}
.upload-buttons {
  cursor: pointer;
  position: absolute;
  background-color: #3a39396e;
  border-radius: 5px;
  border: none;
  display: block;
  width: 30px;
  height: 30px;
  top: 10px;
  z-index: 100;
}

.upload-image {
  left: 10px;
}

.upload-buttons:hover {
  background-color: #229ed7c0;
  transition: 0.3s;
}

.image-container {
  justify-self: center;
  align-self: center;
  background-color: #0000003b;
  overflow: hidden;
  border: 2px dashed #00000000;
}
.hover {
  border: 2px dashed #229ed7;
  opacity: 0.7;
}

.upload-buttons.moving {
  background-color: #229ed7;
}

#image-container.moving {
  cursor: grab;
}

.bar-graph-legend-os {
  height: 200px;
  position: absolute;
  right: 7px;
  top: 28px;
  z-index: 10;
  background: #5252524b;
  padding: 5px;
  padding-right: 2px;
  border-radius: 10px;
  user-select: none;
  -webkit-user-drag: none;
  pointer-events: none;
}
</style>
