<template>
  <input
    v-if="editable"
    @input="updateProp"
    type="date"
    name="dateofbirth"
    id="dateofbirth"
    class="date-picker"
    v-model="selectedDate"
  />
  <p class="date" v-else>{{ formattedDate }}</p>
</template>

<script>
import { useReportStore } from "/src/store/report.js";

export default {
  props: {
    editable: { type: Boolean },
    id: { type: String },
    bindValue: { type: String },
  },
  data() {
    return {
      selectedDate: "", // Stores the selected date in YYYY-MM-DD format
      reportStore: useReportStore(),
    };
  },

  mounted() {
    if (this.reportStore.report[this.id]) {
      const date = this.reportStore.report[this.id];
      this.selectedDate = this.formatDateToInput(date) || null;
    }
  },

  methods: {
    updateProp(event) {
      this.reportStore.report[this.id] = this.formattedDate;
      if (this.bindValue) {
        this.reportStore.report[this.bindValue] =
          this.calculateAge(this.selectedDate) || 0;
      }
    },
    calculateAge(inputDate) {
      const currentDate = new Date();
      const birthDate = new Date(inputDate);

      if (isNaN(birthDate)) {
        return;
      }

      let age = currentDate.getFullYear() - birthDate.getFullYear();
      const monthDiff = currentDate.getMonth() - birthDate.getMonth();

      if (
        monthDiff < 0 ||
        (monthDiff === 0 && currentDate.getDate() < birthDate.getDate())
      ) {
        age -= 1;
      }

      return age;
    },
    formatDateToInput(inputDate) {
      const months = {
        JAN: "01",
        FEB: "02",
        MAR: "03",
        APR: "04",
        MAY: "05",
        JUN: "06",
        JUL: "07",
        AUG: "08",
        SEP: "09",
        OCT: "10",
        NOV: "11",
        DEC: "12",
      };

      const parts = inputDate.split("-");
      // console.log(inputDate, parts);
      if (parts.length !== 3) {
        return null;
      }

      const day = parts[0];
      const month = months[parts[1]];
      const year = parts[2];

      if (!day || !month || !year) {
        return null;
      }

      const formattedDate = `${year}-${month}-${day}`;
      return formattedDate;
    },
  },
  computed: {
    formattedDate() {
      if (this.selectedDate) {
        const dateParts = this.selectedDate.split("-");
        const year = dateParts[0];
        const month = dateParts[1];
        const day = dateParts[2];
        const monthNames = [
          "JAN",
          "FEB",
          "MAR",
          "APR",
          "MAY",
          "JUN",
          "JUL",
          "AUG",
          "SEP",
          "OCT",
          "NOV",
          "DEC",
        ];

        return `${day}-${monthNames[parseInt(month) - 1]}-${year}`;
      }

      return "DD-Mon-YYYY"; // Handle cases where no date is selected
    },
  },
};
</script>

<style scoped>
.date-picker {
  background: #fff
    url(https://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/calendar_2.png)
    97% 50% no-repeat;
}

.date-picker::-webkit-inner-spin-button {
  display: none;
}

.date-picker::-webkit-calendar-picker-indicator {
  opacity: 0;
}

input.date-picker {
  border: 1px solid #c4c4c4;
  border-radius: 5px;
  background-color: #fff;
  padding: 3px 5px;
  outline: none;
  /* box-shadow: inset 0 3px 6px rgba(0, 0, 0, 0.1); */
  width: 100px;
}

.date {
  margin-bottom: -1px;
  font-weight: bold;
  font-size: 13px;
}
</style>
