<template>
  <div v-if="editable" class="select">
    <select
      :id="id"
      required
      @change="onOptionSelected($event)"
      aria-labelledby="dropDownComponentinReportElement"
    >
      <option value="" disabled selected>{{ selectedOption }}</option>
      <option
        v-for="(option, index) in options"
        :key="index"
        :value="option.value"
      >
        {{ option.label }}
      </option>
    </select>
  </div>
  <div v-else>
    <h5 class="selected-option">{{ selectedOption }}</h5>
  </div>
</template>

<script>
import { useReportStore } from "/src/store/report.js";

export default {
  props: {
    id: {
      type: String,
      required: true,
    },
    options: {
      type: Array,
      required: true,
    },
    editable: { type: Boolean, default: true },
  },
  data() {
    return {
      selectedOption: "Select...",
      reportStore: useReportStore(),
    };
  },
  mounted() {
    if (this.reportStore.report[this.id]) {
      this.selectedOption = this.reportStore.report[this.id];
    }
  },
  methods: {
    onOptionSelected(event) {
      const selectedValue = event.target.value;
      this.selectedOption = selectedValue;
      this.reportStore.report[this.id] = selectedValue;
      // this.$emit("option-selected", this.id, selectedValue);
    },
  },
};
</script>

