  position: relative;
  width: 100px;

  color: black;
  display: flex;
  /* justify-content: center; */
}

select {
  color: black;
  height: 22px;
}

.select select {
  appearance: none;
  -webkit-appearance: none;
  padding: 0px 20px 3px 5px;
