    getTextWidth(text) {
      const element = document.createElement("span");
      element.style.font = "12px Arial";
      element.style.position = "absolute";
      element.style.visibility = "hidden";
      element.textContent = text;
      document.body.appendChild(element);
      const { width } = element.getBoundingClientRect();
      document.body.removeChild(element);
      return width;
    },
    splitStringByPixelWidth(inputString, maxWidthInPixels, charWidthInPixels) {
      if (inputString === "") {
        return [""];
      }
      if (maxWidthInPixels <= 0 || charWidthInPixels <= 0) {
        throw new Error(
          "maxWidthInPixels and charWidthInPixels should be greater than 0"
        );
      }

      const words = inputString.split(" ");
      const lines = [];
      let currentLine = "";
      let currentWidth = 0;

      for (let i = 0; i < words.length; i++) {
        const word = words[i];
        const wordWidth = word.length * charWidthInPixels;

        if (wordWidth <= maxWidthInPixels) {
          if (currentWidth + wordWidth <= maxWidthInPixels) {
            if (currentLine.length > 0) {
              currentLine += " ";
              currentWidth += charWidthInPixels;
            }
            currentLine += word;
            currentWidth += wordWidth;
          } else {
            lines.push(currentLine);
            currentLine = word;
            currentWidth = wordWidth;
          }
        }
      }

      if (currentLine) {
        lines.push(currentLine);
      }

      return lines;
    },

    processSentences(inputString, doc, startX, startY) {
      const hasNewline = /[\r\n]/.test(inputString);

      const linesFromUserInput = inputString.split("\n");

      const linesToAdd = [];

      linesFromUserInput.forEach((line) => {
        const splitByWidth = this.splitStringByPixelWidth(line, 1090, 12);
        linesToAdd.push(...splitByWidth);
      });

      linesToAdd.forEach((line, index) => {
        if (index < 7) {
          doc.text(line, startX, startY + 14 * index);
        }
      });

      // const splitByWidth = this.splitStringByPixelWidth(lines[0], 1080, 12);
      // const splitByWidth2 = this.splitStringByPixelWidth(lines[1], 1080, 12);
      // console.log(linesFromUserInput, linesToAdd);
    },

    processSentencesOLD(inputString, lineWidth, doc, xLocation, initialY) {
      // Helper function to measure the width of the text using a given font
      function getTextWidth(text) {
        const element = document.createElement("span");
        element.style.font = "10px Arial";
        element.style.position = "absolute";
        element.style.visibility = "hidden";
        element.textContent = text;
        document.body.appendChild(element);
        const { width } = element.getBoundingClientRect();
        document.body.removeChild(element);
        return width;
      }

      // Remove any leading/trailing whitespace and split the input into sentences
      let sentences = inputString.trim().split(/(?<=[.!?])|\n/); // Split by ".", "!", "?" including punctuation
      sentences = sentences.map((string) => string.replace(/\n/g, ""));
      // Helper function to split a long sentence into chunks based on the line width
      function splitSentence(sentence, width) {
        const words = sentence.split(" ");
        const chunks = [];
        let currentChunk = words[0];

        for (let i = 1; i < words.length; i++) {
          const testChunk = `${currentChunk} ${words[i]}`;
          const testWidth = getTextWidth(testChunk);

          if (testWidth <= width) {
            currentChunk = testChunk;
          } else {
            chunks.push(currentChunk);
            currentChunk = words[i];
          }
        }

        chunks.push(currentChunk);
        return chunks;
      }

      let logIndex = 0; // Variable to keep track of the index
      let y = initialY;

      // Process each sentence
      sentences.forEach((sentence) => {
        if (getTextWidth(sentence) > lineWidth) {
          const sentenceChunks = splitSentence(sentence, lineWidth);

          sentenceChunks.forEach((chunk) => {
            if (logIndex > 0) {
              y += 14;
            }
            doc.text(chunk, xLocation, y);
            logIndex++;
          });
        } else {
          if (logIndex > 0) {
            y += 14;
          }
          doc.text(sentence, xLocation, y);
          logIndex++;
        }
      });
    },
  },
